<template>
  <div>
    <v-h1 default-back-to="/apps/identifiers">Upload IPA package</v-h1>

    <v-info class="mb-5">
      Make sure that your app complies with <NuxtLink to="https://rtfm.dbservices.to/#/guidelines/app-design">guidelines</NuxtLink> and you correcly
      detect <NuxtLink to="https://rtfm.dbservices.to/#/app-development/detecting-available-features">available features</NuxtLink>,
      <NuxtLink to="https://rtfm.dbservices.to/#/app-development/identifiers">identifiers</NuxtLink> and
      <NuxtLink to="https://rtfm.dbservices.to/#/services-and-features/overview">services</NuxtLink>. modifications.
    </v-info>

    <v-file
      v-model="ipa"
      :extensions="['ipa']"
      :loading="uploadingIPA"
      :progress="uploadingIPAProgress"
      notification-id="add_official_ipa"
    >
      Choose a IPA package or drag it here
    </v-file>
  </div>
</template>

<script setup lang="ts">
import randomize from "randomatic"

const route = useRoute()
const id = computed(() => route.params.id as string)

const ipa = ref<File>()
const uploadingIPA = ref(false)
const uploadingIPAProgress = ref(0)

watchEffect(async () => {
  if (!ipa.value) {
    return
  }

  uploadingIPA.value = true
  const jobID = randomize("a0", 40)

  try {
    await addOfficialIPA(
      {
        scope: id.value,
        ipa: ipa.value,
        job_id: jobID,
      },
      {
        onUploadProgress: (progressEvent) => {
          uploadingIPAProgress.value = calculateUploadProgress(progressEvent)
        },
      }
    )

    navigateTo(`/apps/binary-packages/job/${jobID}`)
  } catch {
    uploadingIPAProgress.value = 0
    uploadingIPA.value = false
  }
})
</script>
